'use client';

import React, { useEffect } from "react";
import SidebarDemo from "./components/SideNav";
import DashboardHeader from "./components/DashboardHeader";
import { useUser } from "@clerk/nextjs";
import { useRouter } from "next/navigation";

export default function DashboardLayout({ children }) {
  const { isSignedIn, isLoaded } = useUser(); // Thêm isLoaded để tránh redirect sớm
  const router = useRouter();

  useEffect(() => {
    // Chỉ redirect sau khi Clerk load xong trạng thái
    if (isLoaded && !isSignedIn) {
      router.push("/sign-in");
    }
  }, [isLoaded, isSignedIn, router]);

  // Trong lúc Clerk đang xác định user (isLoaded = false)
  if (!isLoaded) {
    return (
      <div className="flex items-center justify-center h-screen">
        <p className="text-gray-500">Đang tải...</p>
      </div>
    );
  }

  // Nếu user chưa đăng nhập thì đợi useEffect redirect, tránh render layout
  if (!isSignedIn) return null;

  return (
    <div className="flex flex-col ">
      {/* Sidebar bên trái */}
      <DashboardHeader />
      <div className="h-[90vh]">
        <SidebarDemo />
      </div>
      

      {/* Khu vực nội dung chính */}
      <div className="flex-1 flex flex-col">
        


      </div>
    </div>
  );
}
